import React from 'react'
import ologs from "../images/race_ologhai.jpg"

export default() =>
<div>
    <div className="container">
        <div className="row">
            <div className="col-lg-4">
                <img src={ologs} className="race-profile"/>
            </div>
            <div className="col-lg-8">
                <h1>Olog-Hais</h1>
                <p>
                They were bred by Sauron in the late Third Age, possibly for the same reason Saruman bred the Uruk-hai, for improvement and less vulnerability to sunlight. They lived primarily in Mordor and in southern Mirkwood.
                </p>
                <h1>Abilities</h1>
                <p>
                    Olog-Hais receive a bonus to bashing and have a natural health and move regeneration. This race is intended for seasoned players 
                    because of their complete lack of mystic and mage skills. Olog-Hais are an extreme warrior race and can't be targetted by any healing spells due to their
                    natural regeneration.
                </p>
                <table className="table">
                    <thead>
                        <tr>
                            <th>Strength</th>
                            <th>Intelligence</th>
                            <th>Will</th>
                            <th>Dexterity</th>
                            <th>Constitution</th>
                            <th>Learning Ability</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>Very High</td>
                            <td>Very Low</td>
                            <td>Very Low</td>
                            <td>Low</td>
                            <td>Very High</td>
                            <td>Very Low</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</div>


