import React from 'react';
import YellowButtonMD from '../components/Buttons/YellowButtonMD';

export default({ data }) =>
<div>
    <div className="jumbotron logs">
        <div className="container">
            <h1>Logs</h1>
            <p></p>
        </div>
    </div>
    <div className="container">
        <div className="row">
            {data.allMarkdownRemark.edges.map(({ node }) =>
                <div key={node.id} className="col-lg-12" style={{display: 'block'}}>
                    <h1>{node.frontmatter.title}</h1>
                    <small>{node.frontmatter.date}</small>
                    <p>{node.excerpt}</p>
                    <p><YellowButtonMD path={node.frontmatter.path} >View Details &raquo;</YellowButtonMD></p>
                </div>
            )}
        </div>
    </div>
</div>

export const query = graphql `
query LogPosts {
    allMarkdownRemark(
        sort: {fields: [frontmatter___date], order: DESC},
        filter: {fileAbsolutePath: {regex: "/(logs)/.*\\.md$/"}}
        ) {
        edges {
            node {
                id
                frontmatter {
                    title
                    date(formatString: "MMMM DD, YYYY")
                    path
                    author
                }
                excerpt
            }
        }
    }
}
`


