import React from 'react'
import urukhais from "../images/race_urukhai.jpg"

export default() =>
<div>
    <div className="container">
        <div className="row">
            <div className="col-lg-4">
                <img src={urukhais} className="race-profile" />
            </div>
            <div className="col-lg-8">
                <h1>Uruk-Hais</h1>
                <p>
                <strong>Uruk-hais</strong> were first created by Saruman late in the Third Age using his dark arts in the pits of Isengard. In the War of the Ring, the Uruk-hai made up a large part of Saruman's army, together with the Dunlendings, man-enemies of Rohan. There are suggestions that the Uruk-hai were the result of crossbreeding orcs and men. There were other creatures in Saruman's armies, and under his command in the Shire, that appear to have been hybrids. "Half-orcs" were as tall as Men and are never described simply as orcs, as the Uruk-hai frequently are. Saruman's army of Uruk-hai fought against King Théoden of Rohan and his people at the Helm’s Deep.
                </p>
                <h1>Abilities</h1>
                <p>Uruk-hais are affected by the sun which weakings their ability to see in their surrounding rooms, and lowers their ability to dodge and parry. They make excellent warriors and even deadlier rangers.</p>
                <table className="table">
                    <thead>
                        <tr>
                            <th>Strength</th>
                            <th>Intelligence</th>
                            <th>Will</th>
                            <th>Dexterity</th>
                            <th>Constitution</th>
                            <th>Learning Ability</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>Normal</td>
                            <td>Very Low</td>
                            <td>Very Low</td>
                            <td>Normal</td>
                            <td>High</td>
                            <td>Very Low</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</div>


