import React, { Component } from 'react';
import SkillsList from '../../components/SkillsList';
import { PageHeader } from 'react-bootstrap';
import RangerSkillsList from '../../components/Classes/RangerSkillsList';

class Ranger extends Component {
    render() {
        return (
            <div className="container">
                <div className="row">
                    <div className="col-lg-12">
                        <PageHeader>
                            Ranger
                        </PageHeader>
                    </div>
                    <div className="col-lg-12">
                        <SkillsList skills={this.props.data} />
                    </div>
                </div>
            </div>
        );
    }
}

export default Ranger;

export const query = graphql `
query RangerSkillsList {
    allMarkdownRemark (
        sort: {fields: [frontmatter___level], order: ASC},
        filter: {frontmatter: { group: { eq: "Ranger"}}}
    ) {
        edges {
            node {
                id
                html
                frontmatter {
                    title
                    syntax
                    group
                    level
                    offensive
                }
            }
        }
    }
}
`


