import React from 'react';
import './YellowButtonSM.css';


class YellowButtonSM extends React.Component
{
    handleClick = () => {
        this.props.handleClassClick(this.props.value);
    }
    render() {
        return (
            <div className="YellowButtonSM-Container">
                <button 
                    className = "YellowButtonSM"
                    onClick={this.handleClick}
                    >
                {this.props.children}
                </button>
            </div>
        );
    }
}

export default YellowButtonSM;


