import React from 'react'
import humans from "../images/race_human.jpg"

export default() =>
<div>
    <div className="container">
        <div className="row">
            <div className="col-lg-4">
                <img src={humans} className="race-profile" />
            </div>
            <div className="col-lg-8">
                <h1>Humans</h1>
                <p>
                The race of Men is the second race of beings created by the supreme God, Ilúvatar, since they awoke at the start of the First Age, at the first rising of the Sun (FA 1), while the elves awoke three Ages before them. They are called the Afterborn (Quenya Atani, Sindarin: Edain) by the Elves.
                </p>
                <p>
                Men bear the Gift of Men, which is mortality, and therefore they age and die when their time comes, and are susceptible to illness and disease. Elves are immortal, in the sense that they are not susceptible to aging and disease. Even if their bodies are slain, their spirits will remain bound to the world for as long as it lasts, and pass to the Halls of Mandos to wait until they are released or the world ends.
                </p>
                <h1>Abilities</h1>
                <p>
                    Due to humans normal statistics they excel at every class and any combination.
                </p>
                <table className="table">
                    <thead>
                        <tr>
                            <th>Strength</th>
                            <th>Intelligence</th>
                            <th>Will</th>
                            <th>Dexterity</th>
                            <th>Constitution</th>
                            <th>Learning Ability</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>Normal</td>
                            <td>Normal</td>
                            <td>Normal</td>
                            <td>Normal</td>
                            <td>Normal</td>
                            <td>Normal</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</div>


