import React, { Component } from 'react';
import YellowButtonSM from '../components/Buttons/YellowButtonSM';


class RCD extends Component {
    constructor(props) {
        super(props);
        this.state = {
            warLevel: 0,
            warCoef: 0,
            ranLevel: 0,
            ranCoef: 0,
            mysLevel: 0,
            mysCoef: 0,
            magLevel: 0,
            magCoef: 0
        };
        this.warPoints = 0;
        this.ranPoints = 0;
        this.mysPoints = 0;
        this.magPoints = 0;
        this.TotalPoints = 150;

        this.handleRecalc = this.handleRecalc.bind(this);
        this.handleClassClick = this.handleClassClick.bind(this);
    }

    handleRecalc(boxNum, event)
    {
        let TempCoe = 0;
        let TempPoi = 0;

        if(boxNum === 0)
        {
            this.setState({warLevel: event});
            TempCoe = (event / 30 * 100);
            TempPoi = ((TempCoe / 10) * (TempCoe / 10));

            if(TempPoi - .01 > Math.floor(TempPoi) && TempPoi < TempPoi + 1)
            {
                TempPoi = Math.ceil(TempPoi);
            }

            if(TempPoi > Math.floor(TempPoi) && TempPoi < Math.floor(TempPoi) + .01)
            {
                TempPoi = Math.floor(TempPoi);
            }

            // this.setState({warPoints: TempPoi});
            this.warPoints = TempPoi;
            this.setState({warCoef: (Math.pow(TempPoi, .5) * 10)});
        }
        if(boxNum === 2)
        {
            this.setState({ranLevel: event});
            TempCoe = (event / 30 * 100);
            TempPoi = ((TempCoe / 10) * (TempCoe / 10));

            if(TempPoi - .01 > Math.floor(TempPoi) && TempPoi < TempPoi + 1)
            {
                TempPoi = Math.ceil(TempPoi);
            }

            if(TempPoi > Math.floor(TempPoi) && TempPoi < Math.floor(TempPoi) + .01)
            {
                TempPoi = Math.floor(TempPoi);
            }

            // this.setState({warPoints: TempPoi});
            this.ranPoints = TempPoi;
            this.setState({ranCoef: (Math.pow(TempPoi, .5) * 10)});
        }
        if(boxNum === 4)
        {
            this.setState({mysLevel: event});
            TempCoe = (event / 30 * 100);
            TempPoi = ((TempCoe / 10) * (TempCoe / 10));

            if(TempPoi - .01 > Math.floor(TempPoi) && TempPoi < TempPoi + 1)
            {
                TempPoi = Math.ceil(TempPoi);
            }

            if(TempPoi > Math.floor(TempPoi) && TempPoi < Math.floor(TempPoi) + .01)
            {
                TempPoi = Math.floor(TempPoi);
            }

            // this.setState({warPoints: TempPoi});
            this.mysPoints = TempPoi;
            this.setState({mysCoef: (Math.pow(TempPoi, .5) * 10)});
        }
        if(boxNum === 6)
        {
            this.setState({magLevel: event});
            TempCoe = (event / 30 * 100);
            TempPoi = ((TempCoe / 10) * (TempCoe / 10));

            if(TempPoi - .01 > Math.floor(TempPoi) && TempPoi < TempPoi + 1)
            {
                TempPoi = Math.ceil(TempPoi);
            }

            if(TempPoi > Math.floor(TempPoi) && TempPoi < Math.floor(TempPoi) + .01)
            {
                TempPoi = Math.floor(TempPoi);
            }

            // this.setState({warPoints: TempPoi});
            this.magPoints = TempPoi;
            this.setState({magCoef: (Math.pow(TempPoi, .5) * 10)});
        }
        if(boxNum === 1)
        {
            // this.setState({warPoints: event.target.value});
            this.warPoints = event.target.value;
            TempCoe = (Math.pow(event.target.value, .5) * 10);
            this.setState({warCoef: TempCoe});
            this.setState({warLevel: (Math.floor(TempCoe / 100 * 30))});
        }
        if(boxNum === 3)
        {
            // this.setState({warPoints: event.target.value});
            this.ranPoints = event.target.value;
            TempCoe = (Math.pow(event.target.value, .5) * 10);
            this.setState({ranCoef: TempCoe});
            this.setState({ranLevel: (Math.floor(TempCoe / 100 * 30))});
        }
        if(boxNum === 5)
        {
            // this.setState({warPoints: event.target.value});
            this.mysPoints = event.target.value;
            TempCoe = (Math.pow(event.target.value, .5) * 10);
            this.setState({mysCoef: TempCoe});
            this.setState({mysLevel: (Math.floor(TempCoe / 100 * 30))});
        }
        if(boxNum === 7)
        {
            // this.setState({warPoints: event.target.value});
            this.magPoints = event.target.value;
            TempCoe = (Math.pow(event.target.value, .5) * 10);
            this.setState({magCoef: TempCoe});
            this.setState({magLevel: (Math.floor(TempCoe / 100 * 30))});
        }
        this.TotalPoints = 150;
        this.TotalPoints -= this.warPoints;
        this.TotalPoints -= this.ranPoints;
        this.TotalPoints -= this.mysPoints;
        this.TotalPoints -= this.magPoints;
    }

    handleClassClick(war, ran, mys, mag) {
        this.handleRecalc(0, war);
        this.handleRecalc(2, ran);
        this.handleRecalc(4, mys);
        this.handleRecalc(6, mag);
    }

    render() {
        return (
            <div>
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12">
                            <h1>RotS Class Designer</h1>
                            <p>Welcome to the RotS online class designer! To design your own class, enter values in either the level or point boxes below; to update the rest of the form, tab or click out of the text box which you most recently altered. If you'd like to use a default or player-recommend class as a starting point, simply click the appropriate button below.</p>
                            <p>Keep in mind that Uruk-Hais receive a flat 3 level deduction to their mage level and that Common Orcs can only utilize two thirds of their real level values.</p>
                            <p>Click one of the buttons below to load a premade class. To create your own click in the text boxes below and type in the numbers you want. Then to adjust the other boxes just click or tab out of the box. If you enter non-numbers it will not work properly and thats your own problem to fix.</p>
                        </div>
                        <div className="col-lg-12">
                            <table className="table-condensed rcd-table">
                                    <thead>
                                        <tr>
                                            <th></th>
                                            <th>Level</th>
                                            <th>Points</th>
                                            <th>Coefficient</th>
                                            <th>Uruk-Hai</th>
                                            <th>Orc</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr>
                                            <th>Warrior</th>
                                            <td><input type="number" value={this.state.warLevel} onChange={(e) => this.handleRecalc(0, e.target.value)} min="0" max="36"/></td>
                                            <td><input type="number" value={this.warPoints} onChange={(e) => this.handleRecalc(1, e)} min="0" max="150" /></td>
                                            <td><input type="number" value={this.state.warCoef} min="0" max="150" readOnly/></td>
                                            <td><input type="number" value={this.state.warLevel} min="0" max="36" readOnly/></td>
                                            <td><input type="number" value={Math.floor((2 * this.state.warLevel) / 3)} min="0" max="36" readOnly/></td>
                                        </tr>
                                        <tr>
                                            <th>Ranger</th>
                                            <td><input type="number" value={this.state.ranLevel} onChange={(e) => this.handleRecalc(2, e.target.value)} min="0" max="36"/></td>
                                            <td><input type="number" value={this.ranPoints} onChange={(e) => this.handleRecalc(3, e)} min="0" max="150"/></td>
                                            <td><input type="number" value={this.state.ranCoef} readOnly min="0" max="123"/></td>
                                            <td><input type="number" value={this.state.ranLevel} min="0" max="36" readOnly/></td>
                                            <td><input type="number" value={Math.floor((2 * this.state.ranLevel) / 3)} min="0" max="36" readOnly/></td>
                                        </tr>
                                        <tr>
                                            <th>Mystic</th>
                                            <td><input type="number" value={this.state.mysLevel} onChange={(e) => this.handleRecalc(4, e.target.value)} min="0" max="36"/></td>
                                            <td><input type="number" value={this.mysPoints} onChange={(e) => this.handleRecalc(5, e)} min="0" max="150"/></td>
                                            <td><input type="number" value={this.state.mysCoef} readOnly min="0" max="123"/></td>
                                            <td><input type="number" value={this.state.mysLevel} min="0" max="36" readOnly/></td>
                                            <td><input type="number" value={Math.floor((2 * this.state.mysLevel) / 3)} min="0" max="36" readOnly/></td>
                                        </tr>
                                        <tr>
                                            <th>Mage</th>
                                            <td><input type="number" value={this.state.magLevel} onChange={(e) => this.handleRecalc(6, e.target.value)} min="0" max="36"/></td>
                                            <td><input type="number" value={this.magPoints} onChange={(e) => this.handleRecalc(7, e)} min="0" max="150"/></td>
                                            <td><input type="number" value={this.state.magCoef} readOnly min="0" max="123"/></td>
                                            <td><input type="number" value={this.state.magLevel - 3} min="0" max="36" readOnly/></td>
                                            <td><input type="number" value={Math.floor((2 * this.state.magLevel) / 3)} min="0" max="36" readOnly/></td>
                                        </tr>
                                    </tbody>
                                    <tfoot>
                                        <tr>
                                            <th>Total</th>
                                            <td></td>
                                            <td>
                                                <input type="number" value={150 - this.TotalPoints} min="0" max="150" readOnly/>
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Remaining</th>
                                            <td></td>
                                            <td><input type="number" value={this.TotalPoints} min="0" max="150" readOnly/></td>
                                        </tr>
                                        <tr>
                                            <td></td>
                                            <td></td>
                                            <td><button onClick={() => this.handleClassClick(0, 0, 0, 0)}>Clear</button></td>
                                        </tr>
                                    </tfoot>
                            </table>
                        </div>
                        <div className="col-lg-12">
                            <h3 className="text-center">Offical Class Coefficients</h3>
                            <ul className="ulRCD">
                                <li>
                                    <button className="YellowButtonSM" onClick={() => this.handleClassClick(30, 15, 12, 9)}>Warrior</button>
                                </li>
                                <li>
                                    <button className="YellowButtonSM" onClick={() => this.handleClassClick(15, 30, 9, 12)}>Ranger</button>
                                </li>
                                <li>
                                    <button className="YellowButtonSM" onClick={() => this.handleClassClick(12, 9, 30, 15)}>Mystic</button>
                                </li>
                                <li>
                                    <button className="YellowButtonSM" onClick={() => this.handleClassClick(9, 12, 15, 30)}>Mage</button>
                                </li>
                                <li>
                                    <button className="YellowButtonSM" onClick={() => this.handleClassClick(33, 15, 6, 0)}>Barbarian</button>
                                </li>
                                <li>
                                    <button className="YellowButtonSM" onClick={() => this.handleClassClick(6, 0, 33, 15)}>Healer</button>
                                </li>
                                <li>
                                    <button className="YellowButtonSM" onClick={() => this.handleClassClick(6, 9, 12, 33)}>Wizard</button>
                                </li>
                                <li>
                                    <button className="YellowButtonSM" onClick={()=> this.handleClassClick(24, 24, 10, 9)}>Swashbuckler</button>
                                </li>
                                <li>
                                    <button className="YellowButtonSM" onClick={()=> this.handleClassClick(10, 9, 24, 24)}>Conjurer</button>
                                </li>
                                <li>
                                    <button className="YellowButtonSM" onClick={()=> this.handleClassClick(19, 18, 18, 18)}>Adventurer</button>
                                </li>
                            </ul>
                        </div>
                        <div className="col-lg-12">
                            <h3 className="text-center">Recommended Coefficients</h3>
                            <ul className="ulRCD">
                                <li>
                                    <button className="YellowButtonSM" onClick={() => this.handleClassClick(30, 20, 6, 3)}>Buffer</button>
                                </li>
                                <li>
                                    <button className="YellowButtonSM" onClick={() => this.handleClassClick(27, 14, 20, 4)}>Explorer</button>
                                </li>
                                <li>
                                    <button className="YellowButtonSM" onClick={() => this.handleClassClick(21, 12, 27, 6)}>PKiller</button>
                                </li>
                                <li>
                                    <button className="YellowButtonSM" onClick={() => this.handleClassClick(30, 15, 15, 0)}>Regen Warrior</button>
                                </li>
                                <li>
                                    <button className="YellowButtonSM" onClick={() => this.handleClassClick(15, 29, 16, 4)}>Sanc Ranger</button>
                                </li>
                                <li>
                                    <button className="YellowButtonSM" onClick={() => this.handleClassClick(7, 6, 24, 26)}>Supporter</button>
                                </li>
                                <li>
                                    <button className="YellowButtonSM" onClick={() => this.handleClassClick(7, 15, 15, 29)}>Sneaky Mage</button>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default RCD;



