import React, { Component } from 'react';
import SkillsList from '../../components/SkillsList';
import { PageHeader } from 'react-bootstrap';

class Warrior extends Component {
    render() {
        return (
            <div className="container">
                <div className="row">
                    <div className="col-lg-12">
                        <PageHeader>
                            Warrior
                        </PageHeader>
                    </div>
                    <div className="col-lg-12">
                        <SkillsList skills={this.props.data} />
                    </div>
                </div>
            </div>
        );
    }
}

export default Warrior;

export const query = graphql `
query WarriorSkillsList {
    allMarkdownRemark (
        sort: {fields: [frontmatter___group, frontmatter___level, frontmatter___title], order: DESC},
        filter: {fileAbsolutePath: {regex: "/(docs)/(classes)/(warrior)/.*\\.md$/"}}
    ) {
        edges {
            node {
                id
                html
                frontmatter {
                    title
                    syntax
                    group
                    level
                    offensive
                }
            }
        }
    }
}
`


