import React from 'react'
import elves from "../images/race_woodelf.jpg"

export default() =>
<div>
    <div className="container">
        <div className="row">
            <div className="col-lg-4">
                <img src={elves} className="race-profile"/>
            </div>
            <div className="col-lg-8">
                <h1>Wood Elves</h1>
                <p>
                The Silvan Elves of Mirkwood (formerly known as Greenwood the Great) were known to be distrustful of Dwarves but friendly to Men, with whom they trade. Mirkwood had been called Greenwood the Great until around the year TA 1050, when the shadow of the Dark Lord Sauron fell upon it, and men began to call it Mirkwood. During his journey to the Lonely Mountain, Bilbo Baggins had to rescue the Dwarves of Thorin II Oakenshield's company from these Elves. These Elves were also one of the armies in the Battle of the Five Armies.
                </p>
                <h1>Attributes</h1>
                <p>
                    Wood Elves receive a bonus to save against poison spells and start with an additonal 15 moves. They also have the ability to see in the dark when the moon is at its fullest. Due to their high intelligence they make excellent mages.
                </p>
                <table className="table">
                    <thead>
                        <tr>
                            <th>Strength</th>
                            <th>Intelligence</th>
                            <th>Will</th>
                            <th>Dexterity</th>
                            <th>Constitution</th>
                            <th>Learning Ability</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>Low</td>
                            <td>High</td>
                            <td>Normal</td>
                            <td>High</td>
                            <td>Low</td>
                            <td>Normal</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</div>


