import React from 'react'
import lhuths from "../images/race_uruklhuth.jpg"

export default() =>
<div>
    <div className="container">
        <div className="row">
            <div className="col-lg-4">
                <img src={lhuths} className="race-profile" />
            </div>
            <div className="col-lg-8">
                <h1>Uruk-Lhuths</h1>
                <p>
                    These creatures are a form of orcs bred by the dreaded Magus, after the War of the Betrayal. They are weaker than normal Uruk-Hais, but more intelligent. They are known as Uruk-Lhuths, or spell-uruks.
                </p>
                <h1>Abilities</h1>
                <p>
                Uruk-Lhuth have an insane number of penalties, and are meant to be a
                challenge for the most elite of our players. First, they have
                no allies in the race war. Their main enemy are the good races
                of Arda, such as Elves and Hobbits, but they hate the Uruks, Common
                Orc, and Harads as well.
                </p>
                <p>
                Uruk-Lhuth may not practice any warrior skills, and have a VERY limited
                select of Ranger and Mystic art. However, they have a very large
                selection of magic spells to choose from, more so than any other evil
                race. They also do not have any shops.
                Uruk-Lhuth are resistant to poison.
                </p>
                <table className="table">
                    <thead>
                        <tr>
                            <th>Strength</th>
                            <th>Intelligence</th>
                            <th>Will</th>
                            <th>Dexterity</th>
                            <th>Constitution</th>
                            <th>Learning Ability</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>Low</td>
                            <td>Normal</td>
                            <td>Low Low</td>
                            <td>Normal</td>
                            <td>Normal</td>
                            <td>Low</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</div>


