import React from 'react'
import orcs from "../images/race_orc.jpg"

export default() =>
<div>
    <div className="container">
        <div className="row">
            <div className="col-lg-4">
                <img src={orcs} className="race-profile" />
            </div>
            <div className="col-lg-8">
                <h1>Common Orcs</h1>
                <p>
                Before Oromë found the Quendi at Cuiviénen, Melkor enslaved some of them. Melkor was the first to learn of the Awakening. He soon began sending evil spirits among the Elves, who planted seeds of doubt against the Valar. It is also rumoured that some of the elves were being captured by a Rider if they strayed too far, and the elves later believed these were brought to Utumno, who cruelly tortured and twisted them into Orcs.
                </p>
                <h1>Abilities</h1>
                <p>Common Orcs have their coefficents reduced by two thirds because they are a weaker race, but they are able to recruit other NPC orcs to follow them and assist in attacks mobs.</p>
                <table className="table">
                    <thead>
                        <tr>
                            <th>Strength</th>
                            <th>Intelligence</th>
                            <th>Will</th>
                            <th>Dexterity</th>
                            <th>Constitution</th>
                            <th>Learning Ability</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>Low</td>
                            <td>Very Low</td>
                            <td>Very Low</td>
                            <td>Low</td>
                            <td>Very Low</td>
                            <td>Very Low</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</div>


