import React from 'react'
import hobbits from "../images/race_hobbit.jpg"

export default() =>
<div>
    <div className="container">
        <div className="row">
            <div className="col-lg-4">
                <img className="race-profile" src={hobbits} />
            </div>
            <div className="col-lg-8">
                <h1>Hobbits</h1>
                <p><strong>Hobbits</strong>, also know as Halflings, are an ancient mortal race that live in Middle-Earth. Although their exact origins are unknown, they were initially found in the northern regions of Middle-Earth and below the Vales of Anduin. At the beginning of the Third Age, Hobbits moved north and west. Most of their race eventually founded the land of the Shire in about the year TA 1601, though one type of Hobbit known as Stoors remained in the Anduin Value.</p>
                <h2>Attributes</h2>
                <p>Hobbits receive a bonus to saving against spells, and are harder to detect while hidden. Due to their small size they aren't capable of bashing doors in.</p>
                <p>Due to their stat assignment they make excellent rangers and good mystics because of their inherient spell save.</p>
                <table className="table">
                    <thead>
                        <tr>
                            <th>Strength</th>
                            <th>Intelligence</th>
                            <th>Will</th>
                            <th>Dexterity</th>
                            <th>Constitution</th>
                            <th>Learning Ability</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>Very Low</td>
                            <td>Low</td>
                            <td>Normal</td>
                            <td>High</td>
                            <td>High</td>
                            <td>Normal</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</div>


