import React, { Component } from 'react';
import Panel from 'react-bootstrap/lib/Panel'

class RangerSkillsList extends Component
{
    render() {
        return (
            <div>
                {this.props.skills.allMarkdownRemark.edges.map(({node}) =>
                    <div className="col-lg-12" key={node.id}>
                        <Panel defaultExpanded>
                            <Panel.Heading>
                                <Panel.Title toggle>
                                </Panel.Title>
                            </Panel.Heading>
                        </Panel>
                        {/* <Panel id={node.id} defaultExpanded>
                            <Panel.Heading>
                                <Panel.Title toggle>
                                    {node.frontmatter.title}
                                </Panel.Title>
                            </Panel.Heading>
                            <Panel.Collapse>
                                <Panel.Body>
                                    <dl className="dl-horizontal">
                                        <dt>Syntax</dt>
                                        <dd>{node.frontmatter.syntax}</dd>
                                        <dt>Group</dt>
                                        <dd>{node.frontmatter.group}</dd>
                                        <dt>Level</dt>
                                        <dd>{node.frontmatter.level}</dd>
                                        <dt>Offensive</dt>
                                        <dd>{node.frontmatter.offensive}</dd>
                                    </dl>
                                    <div dangerouslySetInnerHTML={{__html: node.html}} />
                                </Panel.Body>
                            </Panel.Collapse>
                        </Panel> */}
                    </div>
                )}
            </div>
        );
    }
}

export default RangerSkillsList;


