import React, { Component } from 'react';
import PowersList from '../../components/PowersList';
import { PageHeader } from 'react-bootstrap';

class Mystic extends Component {
    render() {
        return (
            <div className="container">
                <div className="row">
                    <div className="col-lg-12">
                        <PageHeader>
                            Mystic
                        </PageHeader>
                    </div>
                    <div className="col-lg-12">
                        <PowersList skills={this.props.data} />
                    </div>
                </div>
            </div>
        );
    }
}

export default Mystic;

export const query = graphql `
query MysticPowerList {
    allMarkdownRemark (
        sort: {fields: [frontmatter___level], order: ASC},
        filter: {fileAbsolutePath: {regex: "/(docs)/(powers)/.*\\.md$/"}}
    ) {
        edges {
            node {
                id
                html
                frontmatter {
                    title
                    syntax
                    group
                    level
                    min_spirit
                    offensive
                }
            }
        }
    }
}
`


